import { useContext } from 'preact/hooks';
import { BuildContext } from './lib/context';
import tw from 'twin.macro';

const Container = tw.div`flex flex-col items-center justify-center h-screen
  bg-gradient-to-b from-mobalytics-light to-mobalytics text-white`;

const App = () => {

  const { buildData } = useContext(BuildContext);

  return (
    <Container>
      <pre style="max-width: 100%; white-space: break-spaces; word-break: break-word; padding: 1rem;" dangerouslySetInnerHTML={{ __html: JSON.stringify(buildData) }} />
    </Container>
  );
};

export default App;